import classNames from 'classnames'
import { useState } from 'react'
import style from './Loader.module.css'
import './LoaderAnime'

export const Loader = () => {
    const { load, preloader, hide } = style

    const [loading, setloading] = useState({ load: true, display: true })
    const state = classNames(loading.load ? load : `${load} ${hide}`)

    window.addEventListener('load', () => {
        console.log('set')
        setloading({ ...loading, load: false })
        setTimeout(() => setloading({ ...loading, display: false }), 600)

    })
    return (
        <>
            {loading.display && <div data-testid='loader' className={state}>
                <div className={preloader}>
                    <svg viewBox="0 0 160 100" width="100%" xmlns="http://www.w3.org/2000/svg">
                        <g className="dots">
                            <g fill="#e8e4e3">
                                <circle className="dot1" cx="20" cy="50" r="10" />
                                <circle className="dot2" cx="50" cy="50" r="10" />
                                <circle className="dot3" cx="80" cy="50" r="10" />
                                <circle className="dot4" cx="110" cy="50" r="10" />
                                <circle className="dot5" cx="140" cy="50" r="10" />
                            </g>
                        </g>
                    </svg>
                </div>
            </div>}
        </>
    )
}
