import anime from "animejs"

window.addEventListener('load', () => {
    function timeline(d: number) {
        const timeline = anime.timeline({
            duration: 700,
            easing: "easeInOutSine",
            loop: true,
            delay: d * 30
        })
        return timeline
    }
    for (let i = 1; i < 6; i++) {
        timeline(i).add({
            targets: `.dot${i}`,
            cy: [
                { value: 50 },
                { value: 45 },
                { value: 55 },
                { value: 45 },
                { value: 50 },
            ]
        })
    }
})